import React from "react"
import { Link } from "gatsby"

const Book = ({ book }) => {
  const isReadingOrReaded = book.readings.length > 0

  return (
    <li key={book.title} className="book-item">
      <section className="image">
        <Link to={book.link} itemProp="url">
          <img src={book.image} alt={book.title} />
        </Link>
      </section>
      <section>
        <h4>{book.title}</h4>
        <span>
          De{" "}
          <Link to={book.author.link} itemProp="url">
            {book.author.name}
          </Link>
        </span>
        {isReadingOrReaded && (
          <div>
            <span>Lecturas:</span>
            <ul>
              {book.readings.map(r => {
                if (r.end_date && r.end_date !== "") {
                  return (
                    <li key={r.start_date}>
                      <span>
                        Del <time>{r.start_date}</time> al{" "}
                        <time>{r.end_date}</time>
                      </span>
                    </li>
                  )
                } else {
                  return (
                    <li key={r.start_date}>
                      <span>
                        Empezado el <time>{r.start_date}</time>
                      </span>
                    </li>
                  )
                }
              })}
            </ul>
          </div>
        )}
      </section>
    </li>
  )
}

export default Book
