import React, { useState } from "react"

import Book from "./book"

const Books = ({ readed, reading, wantToRead, isFiltering }) => {
  const title = "book-list-container"
  const [showWantToRead, toggleWantToRead] = useState(false)
  const [showReading, toggleReading] = useState(true)
  const [showReaded, toggleReaded] = useState(true)

  return (
    <div key={title} className={title}>
      {wantToRead?.length > 0 && !isFiltering && (
        <>
          <button onClick={() => toggleWantToRead(!showWantToRead)}>
            <h2>
              <span
                className={
                  showWantToRead ? "text-shadow" : "text-shadow-disabled"
                }
              >
                Quiero leer
              </span>
            </h2>
          </button>
          {showWantToRead && (
            <ol className="book-list">
              {wantToRead.map(b => (
                <Book key={b.title} book={b} />
              ))}
            </ol>
          )}
        </>
      )}
      {reading.length > 0 && !isFiltering && (
        <>
          <button onClick={() => toggleReading(!showReading)}>
            <h2>
              <span
                className={showReading ? "text-shadow" : "text-shadow-disabled"}
              >
                Leyendo
              </span>
            </h2>
          </button>
          {showReading && (
            <ol className="book-list">
              {reading.map(b => (
                <Book key={b.title} book={b} />
              ))}
            </ol>
          )}
        </>
      )}
      <button onClick={() => toggleReaded(!showReaded)}>
        <h2>
          <span className={showReaded ? "text-shadow" : "text-shadow-disabled"}>
            Leídos
          </span>
        </h2>
      </button>
      {showReaded && (
        <ol className="book-list">
          {readed.map(b => (
            <Book key={b.title} book={b} />
          ))}
        </ol>
      )}
    </div>
  )
}

export default Books
