import React, { useState } from "react"
import Select from "react-select"

import Layout from "./layout"
import SEO from "./seo"
import Books from "./books"
import Filters from "./filters"

const FILTER = {
  year: "year",
  title: "title",
  author: "author",
  genre: "genre",
}

const Library = ({ location, library }) => {
  const title = library.name
  const {
    readingBooks,
    readedBooks,
    authorSelect,
    titleSelect,
    genreSelect,
  } = getBooksProperties(library)

  const sortedBooks = readedBooks.sort(
    (a, b) =>
      new Date(b.readings[0].end_date) - new Date(a.readings[0].end_date)
  )

  const lastReading = sortedBooks[sortedBooks.length - 1].readings[0]
  const lastYear = lastReading.end_date
    ? new Date(lastReading.end_date).getFullYear()
    : new Date(lastReading.start_date).getFullYear()
  const now = new Date()

  const selectYears = Array.from(
    { length: now.getFullYear() - lastYear + 1 },
    (_, i) => {
      const y = now.getFullYear() - i
      return { value: y, label: y }
    }
  )

  const [filters, handleFilters] = useState({ year: selectYears[0] })

  const [books, updateBooks] = useState(
    readedBooks.filter(b => {
      const lastReading = b.readings[0]
      const year = lastReading.end_date
        ? new Date(lastReading.end_date).getFullYear()
        : new Date(lastReading.start_date).getFullYear()
      return year === now.getFullYear()
    })
  )

  const select = (
    <div className="select-year">
      {selectYears.length > 1 && (
        <Select
          value={filters.year}
          onChange={y => {
            const f = { ...filters, year: y }
            handleFilters(f)
            updateBooks(
              filterBooks({
                books: readedBooks,
                filters: f,
              })
            )
          }}
          options={selectYears}
          theme={selectTheme}
        />
      )}
    </div>
  )

  return (
    <Layout location={location} title={title}>
      <SEO title={title} />
      <h1>{title}</h1>

      <Filters
        location={location}
        title="Librerías"
        path="/wiki/library"
        select={select}
        triggerFiltersExpanded={isExpanded => {
          if (!isExpanded) {
            const f = { year: selectYears[0] }
            handleFilters(f)
            updateBooks(
              filterBooks({
                books: readedBooks,
                filters: f,
              })
            )
          }
        }}
      >
        <div className="select select-title">
          {filters.title && <span className="filter-name">Título:</span>}
          <Select
            isClearable
            placeholder="Título"
            options={titleSelect}
            onChange={(v, { action }) => {
              let f = { ...filters }
              if (action === "select-option") {
                delete f[FILTER.year]
                f.title = v
              } else {
                delete f[FILTER.title]
                if (Object.keys(f).length === 0) {
                  f.year = selectYears[0]
                }
              }

              handleFilters(f)
              updateBooks(
                filterBooks({
                  books: readedBooks,
                  filters: f,
                })
              )
            }}
            theme={selectTheme}
          />
        </div>
        <div className="select select-author">
          {filters.author && <span className="filter-name">Autor:</span>}
          <Select
            isClearable
            placeholder="Autor"
            isDisabled={filters.title}
            options={authorSelect}
            onChange={(v, { action }) => {
              const f = { ...filters }
              if (action === "select-option") {
                delete f[FILTER.year]
                f.author = v
              } else {
                delete f[FILTER.author]
                if (Object.keys(f).length === 0) {
                  f.year = selectYears[0]
                }
              }

              handleFilters(f)
              updateBooks(
                filterBooks({
                  books: readedBooks,
                  filters: f,
                })
              )
            }}
            theme={selectTheme}
          />
        </div>
        <div className="select select-genre">
          {filters.genre && <span className="filter-name">Género:</span>}
          <Select
            isClearable
            placeholder="Género"
            isDisabled={filters.title}
            options={genreSelect}
            onChange={(v, { action }) => {
              const f = { ...filters }
              if (action === "select-option") {
                delete f[FILTER.year]
                f.genre = v
              } else {
                delete f[FILTER.genre]
                if (Object.keys(f).length === 0) {
                  f.year = selectYears[0]
                }
              }

              handleFilters(f)
              updateBooks(
                filterBooks({
                  books: readedBooks,
                  filters: f,
                })
              )
            }}
            theme={selectTheme}
          />
        </div>
      </Filters>

      <Books
        readed={books}
        reading={readingBooks}
        wantToRead={library.want_to_read}
        isFiltering={filters.title || filters.author || filters.genre}
      />
    </Layout>
  )
}

const selectTheme = theme => ({
  ...theme,
  borderRadius: "var(--radius-1)",
  colors: {
    ...theme.colors,
    primary25: "var(--color-folders)",
    primary: "var(--color-text-light)",
  },
})

const getBooksProperties = library => {
  const readingBooks = []
  const readedBooks = []
  const authorSelect = []
  const titleSelect = []
  const genres = {}

  library.books.forEach(b => {
    if (b.readings[0].end_date) {
      readedBooks.push(b)
      authorSelect.push({ value: b.author.name, label: b.author.name })
      titleSelect.push({ value: b.title, label: b.title })

      if (b.genre && !genres[b.genre]) {
        genres[b.genre] = b.genre
      }
    } else {
      readingBooks.push(b)
    }
  })

  return {
    readingBooks,
    readedBooks,
    authorSelect,
    titleSelect,
    genreSelect: getGenreSelect(genres),
  }
}

const getGenreSelect = genres => {
  const genreSelect = []

  for (let k in genres) {
    genreSelect.push({ value: k, label: k })
  }

  return genreSelect
}

const filterBooks = ({ books, filters }) =>
  books.filter(b => {
    if (filters.year) {
      for (let r of b.readings) {
        if (
          new Date(r.end_date).getFullYear() ===
          new Date(filters.year.value + "").getFullYear()
        ) {
          return true
        }
      }
      return false
    } else if (filters.title) {
      return filters.title.value === b.title
    } else {
      for (let k in filters) {
        if (k === FILTER.author) {
          if (filters.author.value !== b[k]?.name) {
            return false
          }
        } else {
          if (filters[k].value !== b[k]) {
            return false
          }
        }
      }
      return true
    }
  })

export default Library
